import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	data() {
		return {
			checked: false,
			checked1: false,
			checked2: false,
			checked3: false,
			checked4: false,
			userinfo:[],
			datalist:[],
			checklist:[]
		}
	},
	onLoad() {
	
		this.getdata()
	},
	methods: {
		choice_check(types){
			console.log(types)
			let _self = this
			if(_self.checked==false){
				return _self.common.callback('请先开放体验家')
			}
			if(types.detail.value.length>0){
				// 选中
				_self.checklist[parseInt(types.target.dataset.index)-1] = types.target.dataset.index
				uni.request({
					url:_self.common.websiteUrl+"experhome_owners_uptimes",
					header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
					method:"post",
					data:{'user_id':_self.userinfo.id,'type':_self.checklist.join(',')},
					success: (res) => {
						if(res.data.code==200){
							_self.common.callback('设置成功')
						}else{
							_self.checklist[parseInt(types.target.dataset.index)-1] = 0
							_self.common.network()
						}
					},fail() {
						_self.checklist[parseInt(types.target.dataset.index)-1] = 0
						_self.common.network()
					}
				});
				
				console.log(_self.checklist)
			}else{
				
				_self.checklist[parseInt(types.target.dataset.index)-1] = 0
				uni.request({
					url:_self.common.websiteUrl+"experhome_owners_uptimes",
					header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
					method:"post",
					data:{'user_id':_self.userinfo.id,'type':_self.checklist.join(',')},
					success: (res) => {
						if(res.data.code==200){
							_self.common.callback('设置成功')
						}else{
							_self.checklist[parseInt(types.target.dataset.index)-1] = types.target.dataset.index
							_self.common.network()
						}
					},fail() {
						_self.checklist[parseInt(types.target.dataset.index)-1] = types.target.dataset.index
						_self.common.network()
					}
				});
				console.log(_self.checklist)
			}
			
			
		},
		openclose(e){
			let _self = this
			let title = '';
			if(e==true){
				_self.checked = true
				title = '确认开放体验家吗?';
			}else{
				title = "关闭后预约时间需重新设定,是否确认关闭?"
				_self.checked = false
			}
			
			uni.showModal({
			    title: '温馨提示',
			    content: title,
			    success: function (res) {
			        if (res.confirm) {
			            uni.request({
			            	url:_self.common.websiteUrl+"experhome_owners_openclose",
			            	header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
			            	method:"post",
			            	data:{'user_id':_self.userinfo.id,'type':_self.checked},
			            	success: (res) => {
			            		if(res.data.code==200){
									if(res.data.data==6){
										_self.checked1 = false
										_self.checked2 = false
										_self.checked3 = false
										_self.checked4 = false
									}else{
										_self.checked1 = true
										_self.checked2 = true
										_self.checked3 = true
										_self.checked4 = true
									}
			            			_self.common.callback('设置成功')
			            		}else{
									_self.checked = true
			            			_self.common.network()
			            		}
			            	},fail() {
			            		_self.common.network()
			            	}
			            });
			        } else if (res.cancel) {
			            _self.checked = true
						console.log('取消')
			        }
			    }
			});
			console.log(e)
		},
		get_userinfo(){
			return new Promise((resolve,reject)=>{
				uni.getStorage({
					key: 'userinfo',
					success:(res)=>{
						this.userinfo = res.data
						resolve('suc')
					},fail() {
						resolve('err')
					}
				});	
				
			})
		},
		async getdata(){
			let _self = this
			await _self.get_userinfo()
			// console.log('这里'+self.userinfo.id)
			uni.request({
				url:_self.common.websiteUrl+"experhome_owners_settime",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{'user_id':_self.userinfo.id},
				success: (res) => {
					if(res.data.code==200){
						_self.datalist = res.data.data
						if(_self.datalist.makeapp==6){
							_self.checked = false
						}else{
							_self.checked = true
						}
						let timestr = _self.datalist.makeapp
						let time = timestr.split(',')
						_self.checklist = time
						console.log(time.indexOf('1'))
						if(time.indexOf('1')>=0){
							_self.checked1 = true
						}
						if(time.indexOf('2')>=0){
							_self.checked2 = true
						}
						if(time.indexOf('3')>=0){
							_self.checked3 = true
						}
						if(time.indexOf('4')>=0){
							_self.checked4 = true
						}
						
						
						console.log(time)
						console.log(res.data.data)
					}else{
						_self.common.network()
					}
				}
			});
		},
		back(){
			uni.navigateTo({
				url: '../visitmanagement/visitmanagement'
			});
		}
		
	},
	components:{
		uniNumberBox
	}
}
